@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font-family: 'Poppins', sans-serif;
}
.card{
    margin:2% 10px;
}
.card-wrapper{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin:5% 0;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
    padding:3% 6%;
}
.product-wrapper{
    margin:7% 0;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}
.previewDiv{
    width:40%;
}
#preview{
    width:70%;
    
}
.products-details-div{
    width:45%;
    padding:2% 4%;
}
.photos{
    width:15%;
    margin:2%;
    cursor: pointer;
    padding:2px;
    
}
.activee{
    border-radius: 5px;
    border:2px solid rgb(49, 121, 49)
}
#loader{
    position: fixed;
    right:50%;
    top:50%;
}
#cart{
    margin-top:5%;
    border:none;
    padding:2% 1%;
    background-color: #009688;
    color:white;
    border-radius:5px;
    cursor: pointer;
}
.cartImages{
    width:100px;
    height:100px;
    
}
.cartDiv{
    background-color: white;
    margin:2% 0 2% 3%;
    padding:1% 2%;
    width:25%;
    box-shadow: 1px 3px 5px grey;
    border-radius: 15px;
}
.imageContainer{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}
.rightSec{
    position:fixed;
    right:35%;
    top:25%;
}
button{
    margin-top:2%;
    background-color: #009688;
    border:none;
    color:white;
    padding:4% 6%;
    border-radius: 5px;
}
